.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.canvas {
  background: transparent;
  z-index: 21;
  position: absolute;
}

.box {
  height: 100vh;
  width: 100vw;
  background-repeat : no-repeat;
}

.image-box {
  width:100vw;
  height:100vh;
  overflow:hidden;
  margin:0 auto;
}

.image-thumbnail {
  width:100%;
  height:100%;
  background-size: cover;
  z-index: 1;
  position: absolute;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
